import { Location } from '@angular/common';
import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import {
  ClipboardService,
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetaDataType,
  Tab,
  UserPreferencesService,
} from 'shared-code-lib';
import { IRadioButtonOption } from '../../../../../../Bamba.Shared/src/models/radio-buttons-group';
import { FaxUserProfile } from '../../blocks/InterFAX/Models/admin';
import { SupportedValues } from '../../blocks/InterFAX/Models/outbound';
import { InterFAXService } from '../../blocks/services/interfax.service';
import { ApiResults, Control, ExecuteCriteria } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-interfax-fax-user-profile',
  templateUrl: './interfax-fax-user-profile.component.html',
  styleUrls: ['./interfax-fax-user-profile.component.scss'],
})
export class InterfaxFaxUserProfileComponent implements OnInit, OnDestroy {
  @ViewChild('spinnerWrapper', { static: true }) spinnerWrapper: ElementRef;
  rightControls: Array<Control> = [];
  saveButton: Control;
  cancelButton: Control;

  errors = new Array<string>();

  userProfile: FaxUserProfile;
  supportedValues: SupportedValues;

  fieldUsername: FieldDefinition;
  fieldEmail: FieldDefinition;
  fieldName: FieldDefinition;
  fieldCSID: FieldDefinition;
  fieldNotes: FieldDefinition;
  fieldTimeZone: FieldDefinition;

  outboundServiceStatus = '';
  outboundServiceStatusIcon = '';
  inboundServiceStatus = '';
  inboundServiceStatusIcon = '';

  userType = '';
  userTypeIcon = '';
  show = false;

  tabs: Tab[] = [
    new Tab('properties', this.translateService.instant('fax-user-profile.properties')),
    new Tab('preview', this.translateService.instant('fax-user-profile.preview')),
  ];
  activeTab: string = 'properties';

  ExecuteCriteria = ExecuteCriteria;
  markReadPreference: ExecuteCriteria;
  markReadPreferenceOptions: IRadioButtonOption<ExecuteCriteria>[] = [];

  subscriptions = new Subscription();
  selectedUser: string;

  constructor(
    private translateService: TranslateService,
    private spinnerService: SpinnerStateService,
    private interfaxService: InterFAXService,
    private toastrService: ToastrService,
    private userPreferencesService: UserPreferencesService,
    private location: Location
  ) {}

  ngOnInit(): void {
    this.load();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  private load = () => {
    this.spinnerService.show(this.translateService.instant('Common.LoadingData'), document.documentElement);
    this.createButtons();

    this.markReadPreference = this.userPreferencesService.getPreference(
      'mark-read-preference',
      ExecuteCriteria.OnSelection
    );

    this.markReadPreferenceOptions = this.getMarkReadPreferenceOptions();

    this.subscriptions.add(
      this.interfaxService.getOutboundSupportedValues(this.supportedValuesLoaded, this.loadFailed)
    );
  };

  getMarkReadPreferenceOptions(): IRadioButtonOption<ExecuteCriteria>[] {
    return [
      {
        label: this.translateService.instant('fax-user-profile.mark-read-preference.on-selected'),
        value: ExecuteCriteria.OnSelection,
      },
      {
        label: this.translateService.instant('fax-user-profile.mark-read-preference.after-5-seconds'),
        value: ExecuteCriteria.TimeAfterSelection,
      },
      {
        label: this.translateService.instant('fax-user-profile.mark-read-preference.on-selection-change'),
        value: ExecuteCriteria.DeSelected,
      },
      {
        label: this.translateService.instant('fax-user-profile.mark-read-preference.manually'),
        value: ExecuteCriteria.NoOp,
      },
    ];
  }

  supportedValuesLoaded = (sv: SupportedValues): void => {
    this.supportedValues = sv;

    this.subscriptions.add(this.interfaxService.getFaxUserProfile(this.loaded, this.loadFailed));
  };

  loaded = (up: FaxUserProfile): void => {
    this.spinnerService.hide();
    this.userProfile = up;
    this.createFields();
    this.loadServiceStatus();
    this.show = true;
  };

  loadFailed = (err: any) => {
    //todo: should show error probably
    this.spinnerService.hide();
  };

  loadServiceStatus = (): void => {
    this.outboundServiceStatus = this.translateService.instant(
      this.userProfile.outboundEnabled
        ? 'fax-user-profile.outbound-service-enabled'
        : 'fax-user-profile.outbound-service-none'
    );

    this.inboundServiceStatus = this.translateService.instant(
      this.userProfile.inboundEnabled
        ? 'fax-user-profile.inbound-service-enabled'
        : 'fax-user-profile.inbound-service-none'
    );

    this.outboundServiceStatusIcon = this.userProfile.outboundEnabled ? 'far fa-check-circle' : 'far fa-circle';
    this.inboundServiceStatusIcon = this.userProfile.inboundEnabled ? 'far fa-check-circle' : 'far fa-circle';

    this.userTypeIcon = this.userProfile.userType == 'Normal' ? 'fa-user' : 'fa-user-shield';
    this.userType = this.translateService.instant(
      this.userProfile.userType == 'Normal'
        ? 'fax-user-profile.user-type-normal'
        : 'fax-user-profile.user-type-account-manager'
    );
  };

  onSave = (): void => {
    this.spinnerService.show(this.translateService.instant('Common.LoadingData'), document.documentElement);
    this.subscriptions.add(this.interfaxService.setFaxUserProfile(this.userProfile, this.saved, this.saveFailed));
  };

  saved = (r: ApiResults): void => {
    this.spinnerService.hide();

    if (r.success) {
      this.userPreferencesService.setPreference('mark-read-preference', this.markReadPreference);
      this.toastrService.success(this.translateService.instant('fax-user-profile.save-success'));
      this.returnToPreviousPage();
    } else {
      this.toastrService.error(this.translateService.instant('fax-user-profile.save-fail'));
      this.errors = r.errors;
    }
  };

  saveFailed = (err: Error): void => {
    this.spinnerService.hide();
    this.toastrService.error(this.translateService.instant('fax-user-profile.save-fail'));
    this.errors.push(err.message);
  };

  onCancel = (): void => {
    this.show = false;
    this.returnToPreviousPage();
  };

  returnToPreviousPage = (): void => {
    this.location.back();
  };

  private createButtons() {
    this.saveButton = new Control(
      this.onSave,
      this.translateService.instant('admin-tenant.save'),
      'btn btn-primary btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    this.cancelButton = new Control(
      this.onCancel,
      this.translateService.instant('admin-tenant.cancel'),
      'btn btn-light btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.cancelButton);
  }

  createFields = (): void => {
    this.fieldUsername = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-user-properties.username'),
      placeholder: '',
      required: true,
    });

    this.fieldEmail = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-user-properties.email'),
      placeholder: '',
      required: false,
    });

    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-user-properties.name'),
      placeholder: '',
      required: false,
    });

    this.fieldNotes = new FieldDefinition({
      type: MetaDataType.LargeText,
      label: this.translateService.instant('fax-user-properties.notes'),
      placeholder: '',
      required: false,
    });

    this.fieldCSID = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-user-properties.csid'),
      placeholder: '',
      required: false,
    });

    this.fieldTimeZone = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('fax-user-properties.timezone'),
      placeholder: '',
      required: false,
      datasource: this.getTimeZoneDataSource(),
    });
  };

  getTimeZoneDataSource = (): FixedDataSource => {
    const options = new Array<IFieldOptions>();

    for (const tz of this.supportedValues.timeZones) {
      options.push({ name: tz, value: tz });
    }

    return new FixedDataSource(options);
  };

  onCopyToClipboard = (): void => {
    ClipboardService.copyToClipboard(this.userProfile.formattedPhoneNumber);

    this.toastrService.info(this.translateService.instant('general.copied-to-clipboard'));
  };

  onTabSelected = (id: string) => {
    this.activeTab = id;
  };

  onReadPreferenceSelected = (id: string) => {
    this.markReadPreference = Number(id);
  };
}
