import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { InterfaxFaxUserProfileComponent } from './interfax-fax-user-profile.component';

const routes: Routes = [
  {
    path: '',
    data: { type: 'properties' },
    component: InterfaxFaxUserProfileComponent,
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class InterfaxFaxUserProfileRoutingModule { }
